import React from 'react';
import PropTypes from 'prop-types';
import { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';

import {Text, Panel, PanelHeader, Header, Button, Group, Cell, Div, FormItem, Input} from '@vkontakte/vkui';

const Registration = ({id, fetchedUser, go}) => {
    const [state1, setState1] = useState(true);
    const [state2, setState2] = useState(false);
    const textInput = React.createRef();
    const submit = () => {
        fetch(`https://crypack.000webhostapp.com/new_user.php?id=${fetchedUser['id']}&nick=${textInput.current.value}`);
        setState1(false);
        setState2(true);
    }
    
    return (
        <Panel id={id}>
            <PanelHeader>Регистрация</PanelHeader>
            <Group>
                <FormItem top='Введи свой ник'>
                    <Input getRef={textInput} type='text'/>
                </FormItem>
                <FormItem>
                    <Button stretched disabled={state2} mode='commerce' onClick={submit}>Зарегистрироваться</Button>
                </FormItem>
                <FormItem>
                    <Button stretched disabled={state1} onClick={go} data-to='home'>Перейти в приложение</Button>
                </FormItem>
            </Group>
        </Panel>
    );
};

export default Registration;