import React from 'react';
import PropTypes from 'prop-types';
import { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';

import { Panel, PanelHeader, Button, Div, PanelHeaderBack, Text, Link} from '@vkontakte/vkui';

const Help = ({id, go}) => {
    useEffect(() => {
		bridge.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
	}, []);
    
    return (
    <Panel id={id}>
        <PanelHeader left={<PanelHeaderBack onClick={go} data-to='home'></PanelHeaderBack>}>Помощь</PanelHeader>
        <Div>
                <Text>Не знаю, что ты тут ищешь.</Text>
        </Div>
    </Panel>
    );
};

export default Help;